
.pay_h1{
    font-size: 4.3vmax;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.pay_input_none{
    display: none;
}


.pay_h4{
    font-size: 2.4vmax;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #333;
    text-transform: capitalize;
}

.pay_span{
    font-size: 2.4vmax;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #333;
}

.pay_span:hover{
    color: green;
}

.pay_input_btn{
   width: 35px;
   height: 35px;
   margin: 3%;
   cursor: pointer; 
}




.pay_btn{
   
    border-radius: 100px;
    text-transform: uppercase;
    background-image: radial-gradient(93% 87% at 87% 89%, rgba(0, 0, 0, 0.23) 0%, transparent 86.18%), radial-gradient(66% 87% at 26% 20%, rgba(255, 255, 255, 0.41) 0%, rgba(255, 255, 255, 0) 69.79%, rgba(255, 255, 255, 0) 100%);
    box-shadow: 2px 19px 31px rgb(0 0 0 / 20%);
    border: none;
    background-color: #3BB77E;
    color: white;
    font: 300 4vmax "Roboto";
    width: 50%;
    padding: 1vmax;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
    margin: 9%;
}