  /* cards */
  .cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(308px, 1fr));
    grid-gap: 20px;
  }

  .card {
    background: rgba(255, 249, 248, 0.6);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    padding: 20px;
    display: grid;
    grid-template-columns: 250px 1fr;
    grid-gap: 10px;
    color: var(--title);
    filter: drop-shadow(2px 4px 6px black);
  }

  .card-info-wrapper {
    height: 250px;
    display: grid;
  }

  .card-info-wrapper>h2 {
    margin: -3px 0 0 0;
    font-size: 1rem;
  }

  .card-info-wrapper>h3 {
    margin: 5px 0 0 0;
    font-size: 0.9rem;
  }

  .card p {
    color: var(--text);
    letter-spacing: 0.2px;
    margin-top: 0;
    font-size: 0.8rem;

  }

  .card .icons-wrapper {
    align-self: end;
    margin: 0 0 -7px 0;
    display: flex;
    justify-content: space-between;
  }

  .card .icons-wrapper>a {
    text-decoration: none;
  }

  .card-img {
    width: 40%;
    border-radius: 50%;
  }

  /* style svg icons */
  svg[class*="icon-"] {
    width: 23px;
    height: 23px;
    fill: rgb(83, 94, 126);
  }

  .icon-twitter:hover {
    fill: rgb(29, 161, 242);
  }

  .icon-github:hover {
    fill: rgb(36, 41, 46);
  }

  .icon-behance:hover {
    fill: rgb(0, 87, 255);
  }

  .icon-codepen:hover {
    fill: rgb(19, 20, 23);
  }

  .icon-dribble:hover {
    fill: rgb(234, 76, 137);
  }

  .icon-vimeo:hover {
    fill: rgb(19, 147, 205);
  }

  .icon-instagram:hover {
    fill: rgb(193, 53, 132);
  }

  .icon-linkedin:hover {
    fill: rgb(40, 103, 178);
  }


  /* button  */

  .bbutton {
    position: relative;
    font-family: inherit;
    font-size: 18px;
    border-radius: 40em;
    width: 16em;
    height: 3em;
    z-index: 1;
    color: white;
    overflow: hidden;
    border: none;
    margin-left: 38%;
    margin-top: 10%;
    box-shadow: 0 0 20px red;
    filter: drop-shadow(0 0 20px green);
  }
  
  .bbutton .text {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    line-height: 3em;
    border-radius: 40em;
    border: none;
    background: linear-gradient(rgba(255, 255, 255, 0.473), rgba(150, 150, 150, 0.25));
    z-index: 1;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
  }
  
  .bbutton .blob {
    position: absolute;
    z-index: -1;
    border-radius: 5em;
    width: 11em;
    height: 3em;
    transition: transform .3s ease-in-out, background .3s ease-in-out;
  }
  
  .bbutton .blob:nth-child(2) {
    left: 0em;
    top: 0;
    background: #ff930f;
  }
  
  .bbutton .blob:nth-child(3) {
    left: 1.8em;
    top: 0;
    z-index: -1;
    background: #bf0fff;
  }
  
  .bbutton .blob:nth-child(4) {
    left: 4em;
    top: -1em;
    background: #ff1b6b;
  }
  
  .bbutton .blob:nth-child(5) {
    left: 4.3em;
    top: 1.6em;
    background: #0061ff;
  }
  
  .bbutton:hover .blob:nth-child(2) {
    background: #0061ff;
  }
  
  .bbutton:hover .blob:nth-child(3) {
    background: #ff1b6b;
  }
  
  .bbutton:hover .blob:nth-child(4) {
    background: #bf0fff;
  }
  
  .bbutton:hover .blob:nth-child(5) {
    background: #ff930f;
  }
  
  .bbutton:hover .blob {
    transform: scale(1.3);
  }
  
  .bbutton:active {
    border: 2px solid white;
  }
  
  
  
